<template>
  <header class="main-header">
    <div class="logo">
      <a href="#">
        <img src="../assets/img/logo.png" alt="" />
      </a>
    </div>
    <input type="checkbox" class="menu-btn" id="menu-btn" />
    <label for="menu-btn" class="menu-icon">
      <span class="menu-icon__line"></span>
    </label>
    <ul class="nav-links">
      <li class="nav-link">
        <a>
          <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                v-bind="attrs"
                v-on="on"
                class="language"
              >
                <img src="../assets/img/flag.png" alt="" />
                <span class="lang">{{ language }}</span>
                <i class="fas fa-sort-down"></i>
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                v-for="(item, index) in items"
                :key="index"
                @click="language = item.title"
                v-show="item.title != language"
              >
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </a>
      </li>
      <li class="nav-link">
        <a href="#">
          <span class="signin-btn">SIGN IN</span>
        </a>
      </li>
      <li class="nav-link">
        <a href="#">
          <span class="signup-btn">SIGN UP</span>
        </a>
      </li>
      <li class="nav-link">
        <a href="#">
          <span class="wallet-connect">WALLET CONNECT</span>
        </a>
      </li>
    </ul>
  </header>
</template>

<script>
export default {
  data() {
    return {
      items: [
        { title: "Vietnamese" },
        { title: "Japanese" },
        { title: "Spainish" },
        { title: "English" },
      ],
      language: "English",
    };
  },
  mounted() {
    const header = document.querySelector(".main-header");
    window.addEventListener("scroll", () => {
      const scrollPos = window.scrollY;
      if (scrollPos > 10) {
        header.classList.add("scrolled");
      } else {
        header.classList.remove("scrolled");
      }
    });
  },
};
</script>

